<template>
    <div>
        <mdb-container>
        <h2>Achievement<span></span></h2>
        <ul class="mt-5 nav justify-content-center grey lighten-4 py-4">
            <li class="nav-item" v-for="(date, index) in reverseItems" :key="index">
                <a class="nav-link" href="javascript:void(0)" @click="selectView(index)">{{index}}</a>
                </li>
        </ul><br>
        

        <h3>{{ viewDate }}</h3>
        <div v-html="view"></div>
        </mdb-container>

    </div>
</template>

<script>
    import {mdbContainer} from 'mdbvue';
  export default{
    name: 'Achievement',


    data(){
        return{
            view:'',
            viewDate:'',


        }
    },
    components:{
        mdbContainer
    },
    computed:{
      reverseItems() {
        const datas = this.dates;
        const sorted = {}
        var array = [];
        for (let key in datas) {
        //
            //iftrue
            array.push(key);
        }
     
        array.sort()
        array.reverse();
   
        for (var i = 0; i < array.length; i++) {
        /*
        []  []
        */
        sorted[array[i]] = datas[array[i]];
    }
    //

    return sorted;
    

    }
    },
    props:{
        dates:Object,
    },
    methods:{

        selectView(index){
            const key = index
            this.viewDate = index
            this.view = this.dates[key]


        }
    },
    created(){
        const datas = this.dates;
        var array = [];
        for (let key in datas) {
        //
            //iftrue
            array.push(key);
        }
        array.sort()
        array.reverse();
      
        const first = array[0]
   
        this.viewDate = first
        this.view=this.dates[first]
    },
    watch:{
        dates:function () {
        const datas = this.dates;
        var array = [];
        for (let key in datas) {
        //
            //iftrue
            array.push(key);
        }
       
        array.sort()
        array.reverse();
   
        const first = array[0]
   
        this.viewDate = first
        this.view=this.dates[first]

            
        }
    }
  }
</script>