<template>
  <div>
    <h2>Home Settings<span></span></h2>

    <!--  -->
    <mdb-navbar color="white" class="z-depth-0 lighten-4 py-4">
      <mdb-navbar-nav nav center class="" >
        <mdb-nav-item class="m-1 p-1" href="#" @click.stop.prevent="selectTab('eyeCatch')" :class="{'shadow-sm rounded elegant-color active-font':activeTab==='eyeCatch'}"></mdb-nav-item>
        <mdb-nav-item class="m-1 p-1" href="#" @click.stop.prevent="selectTab('main')" :class="{'shadow-sm rounded elegant-color active-font ':activeTab==='main'}"></mdb-nav-item>
        <mdb-nav-item class="m-1 p-1" href="#" @click.stop.prevent="selectTab('news')" :class="{'shadow-sm rounded elegant-color active-font ':activeTab==='news'}"></mdb-nav-item>
      </mdb-navbar-nav>
    </mdb-navbar>
      
      
    <!--  -->
      <mdb-container v-if="activeTab==='eyeCatch'">
      <mdb-row>
        <mdb-col col="sm">
          <div class="my-3">
            <h3></h3>
          </div>
          <!--  -->
          <div class="p-2 border border-light">

            <div class="input-group">
              <div class="custom-file">
                <input type="file" accept="image/*" @change="onFileChange" class="custom-file-input" id="home-inputfile" data-browse="" aria-describedby="inputGroupFileAddon01">
                <label class="custom-file-label" for="inputGroupFile01"></label>
              </div>
            </div>

            <!--  -->
            <div class="position-relative" v-if="uploadedImage">
              <img :src="uploadedImage" style="width:100%;"  />
              <mdb-btn  class="z-depth-2 position-absolute remove-carousel" @click="removeUploadedImg()" color="danger"></mdb-btn>
              
            </div>


            <div class="d-flex flex-row-reverse">
                <mdb-btn :disable="eyeCatchBtnPush" class="z-depth-0 mr-4" @click="updateEyeCatchData" color="success">{{ eyeCatchBtn }}</mdb-btn>
                
            </div>

          </div>
        </mdb-col>
        <mdb-col col="sm">
          <div class="my-3">
            <h3></h3>
          </div>

          <!--  -->

          <mdb-list-group>
            <mdb-list-group-item class="position-relative" v-for="(data,index) in eyeCatchData" :key="index">
                  <img :src="data.src" style="width:100%;" /><br>
                  <mdb-btn  class="z-depth-2 position-absolute remove-carousel" @click="removeCarouselModal=index, carouselModal=true" color="danger"></mdb-btn></mdb-list-group-item>
          </mdb-list-group>
        </mdb-col>

      </mdb-row>
    </mdb-container>
    <!-- <div v-if="activeTab==='eyeCatch'">
      
      





      

      
    </div> -->
    <mdb-container>
    <!--  -->
    <div v-if="activeTab==='main'">
      <div class="my-3">
        <h3></h3>
      </div>

      <!--  -->
      <vue-editor id="editor" useCustomImageHandler @image-added="handleImageAdded" v-model="homeEditor"> </vue-editor>

      <div class="d-flex flex-row-reverse">
          <mdb-btn class="z-depth-0 mr-4" @click="createHome" color="success"></mdb-btn>
      </div>
    </div>
    </mdb-container>


      <mdb-container v-if="activeTab==='news'">
        <mdb-row>
          <mdb-col col="sm">
            <div class="my-3">
              <h3></h3>
            </div>

            <!--  -->
            <div class="mb-2 p-2 border border-light">
              <div class="form-group">
                <label for="news-category"></label>
                <select id="news-category" v-model='homeCategory' class="browser-default custom-select mb-2">
                  <option value="study"></option>
                  <option value="achievement"></option>
                  <option value="member"></option>
                  <option value="acsess"></option>
                </select>
                <label for="news-title"> <span class="ml-1 red-text"></span></label>
                <input type="text" id="news-title" class="form-control mb-1" v-model="homeTitle">
                <label for="news-date"> <span class="ml-1 red-text"></span></label>
                <Datepicker class="mb-1" id="news-date" name="home-date" @closed="pickerClosed()" v-model="homeDate" :bootstrap-styling="true" placeholder="" :language="ja" :format="customFormatter(homeDate)"></Datepicker>

                <label for="news-content"> <span class="ml-1 red-text"></span></label>
                <textarea type="text" id="news-content" class="form-control" v-model="homeContent"></textarea>
              </div>


              <div class="d-flex flex-row-reverse">
                  <mdb-btn :disable="newsBtnPush" class="z-depth-0 mr-4" @click="createNews()" color="success">{{newsBtn}}</mdb-btn>
              </div>

            </div>
          </mdb-col>
          <mdb-col col="sm">
            <!--  -->
            <div class="my-3">
              <h3></h3>
            </div>
            <!-- <mdb-list-group>
              <mdb-list-group-item v-for="(data,index) in reverseItems" :key="index">
                <div>
                  <span>{{data.homeDate}}</span><span class="ml-1">{{data.content}}</span>
                </div>
                <mdb-btn class="z-depth-0" @click="removeModal=index, newsModal=true" outline="danger"></mdb-btn></mdb-list-group-item>
            </mdb-list-group> -->
            <mdb-card class="shadow-none border w-100 border-light" v-for="(data,index) in reverseItems" :key="index" style="width: 18rem;">
              <mdb-card-body>
               
                <mdb-card-title>
                   <span>
                    <mdb-icon v-if="data.category==='achievement'" icon="book" />
                    <mdb-icon v-if="data.category==='member'" icon="users" />
                    <mdb-icon v-if="data.category==='study'" icon="pen" />
                    <mdb-icon v-if="data.category==='acsess'" icon="map-marked-alt" />
                  </span>
                  {{data.homeTitle}}
                </mdb-card-title>
                <mdb-card-title tag="h6" sub class="mb-2 text-muted"> <mdb-icon class="mr-1" icon="calendar-alt" />{{data.homeDate}}</mdb-card-title>
                <mdb-card-text>
                  <p style="white-space: pre-wrap;">{{ data.content }}</p>
                </mdb-card-text>
                <div class="d-flex flex-row-reverse">
                    <mdb-btn class="z-depth-0" @click="removeModal=index, newsModal=true" outline="danger"></mdb-btn>
                </div>
                
              </mdb-card-body>


            </mdb-card>
            <div class="d-flex flex-row-reverse">
                <mdb-btn :disable="eyeCatchBtnPush" class="z-depth-0 mr-4" @click="moreView" color="success">MORE</mdb-btn>
                
            </div>
          </mdb-col>

        </mdb-row>
      </mdb-container>



    <!-- news -->
    <div>
        <mdb-modal :show="newsModal" @close="newsModal = false">
        <mdb-modal-header>
            <mdb-modal-title></mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body></mdb-modal-body>
        <mdb-modal-footer>
            <mdb-btn class="z-depth-0" color="secondary" @click.native="newsModal = false"></mdb-btn>
            <mdb-btn class="z-depth-0" color="danger" @click="removeNewsdata()"></mdb-btn>
        </mdb-modal-footer>
        </mdb-modal>
    </div>
    <!--  -->
    <div>
        <mdb-modal :show="carouselModal" @close="carouselModal = false">
        <mdb-modal-header>
            <mdb-modal-title></mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body></mdb-modal-body>
        <mdb-modal-footer>
            <mdb-btn class="z-depth-0" color="secondary" @click.native="carouselModal = false"></mdb-btn>
            <mdb-btn class="z-depth-0" color="danger" @click="removeCarouseldata()"></mdb-btn>
        </mdb-modal-footer>
        </mdb-modal>
    </div>

    <!--  -->
    <div>
        <mdb-modal :show="formModal" @close="formModal = false">
        <mdb-modal-header>
            <mdb-modal-title></mdb-modal-title>
        </mdb-modal-header>
        <mdb-modal-body>{{formModalMessage}}</mdb-modal-body>
        <mdb-modal-footer>
            <mdb-btn class="z-depth-0" color="success" @click.native="formModal = false">OK</mdb-btn>
        </mdb-modal-footer>
        </mdb-modal>
    </div>

  </div>
</template>

<script>
import firebase from 'firebase';
  import {VueEditor} from "vue2-editor";
import {mdbBtn, 
        mdbListGroup,
        mdbListGroupItem, 
        mdbModal, 
        mdbModalHeader, 
        mdbModalTitle, 
        mdbModalBody, 
        mdbModalFooter,
        mdbNavbar,
        mdbNavbarNav,
        mdbNavItem, 
        mdbContainer,
        mdbRow,
        mdbCol,
        mdbCard,
        mdbCardBody,
        mdbCardTitle,
        mdbCardText,
        mdbIcon
      } from 'mdbvue';

  import moment from 'moment';
  import Datepicker from 'vuejs-datepicker';
  import {ja} from 'vuejs-datepicker/dist/locale'

  export default{
    name: 'editHome',
    components: { VueEditor,
      mdbBtn, 
      mdbListGroupItem,
      mdbListGroup,
      mdbModal, 
      mdbModalHeader, 
      mdbModalTitle, 
      mdbModalBody, 
      mdbModalFooter, 
      mdbNavbar,
      mdbNavbarNav,
      mdbNavItem,
      mdbContainer,
      mdbRow,
      mdbCol,
      Datepicker,
      mdbCard,
      mdbCardBody,
      mdbCardTitle,
      mdbCardText,
      mdbIcon
    },
    data(){
      return{
        ja:ja,
        homeCategory:'study', // 
        homeContent:'', // 
        homeDate:'',
        homeTitle:'', //
        homeEditor:'', // 
        uploadedImage:'', // 
        inputImg:"", // 
        filePath:"", // 
        removeModal:"", // id
        removeCarouselModal:"", // id
        newsModal:false, // 
        carouselModal:false, // 
        formModal:false, // 
        formModalMessage:"", // 
        newsBtn:"", // 
        newsBtnPush:false, // 
        eyeCatchBtn:"",
        eyeCatchBtnPush:false, // 
        activeTab:"eyeCatch" // 

      }
    },
    // app.vue 
    props:{
      newsData:Object,
      homeData:String,
      eyeCatchData:Object,
    },
    // firebase created_at
    computed:{
      reverseItems() {
        const datas = this.newsData;
        const sorted = {};
        var array = [];
        for (let key in datas) {
        //
          if (datas.hasOwnProperty(key)) {
            //iftrue
            array.push(key);
        }}
        array.reverse();
        for (var i = 0; i < array.length; i++) {
            /*
            []  []
            */
            sorted[array[i]] = datas[array[i]];
        }
        //
        return sorted;

      }
    },
    // 
    created(){
      this.homeEditor = this.homeData;
    },
    // 
    watch:{
      homeData:function () {
       
        this.homeEditor = this.homeData;
      }
    },
    methods:{
      // 
      createNews(){
        const homeCategory = this.homeCategory;
        const homeContent = this.homeContent;
        const homeDate = this.homeDate;
        const homeTitle =this.homeTitle;
        const createdAt=firebase.database.ServerValue.TIMESTAMP

      

        // 016
        if(homeContent.length<100 && homeContent.length>0 && homeDate && homeTitle.length<20 && homeTitle.length>0){

          // 
          this.newsBtnPush=true
          this.newsBtn = ""

          // firebase
          firebase.database().ref('news').push({
            content:homeContent,
            category:homeCategory,
            homeDate:homeDate,
            homeTitle:homeTitle,
            created_at:createdAt,
          }).then(()=>{

            // 
            this.newsBtn = ""
            this.newsBtnPush=false;
            this.homeContent = "";
            this.homeCategory = "study";
            this.homeDate="";
            this.formModalMessage="";
            this.formModal=true;

          })
        // 
        }else{
          // 
          this.formModalMessage="1100\n115\n"
          this.formModal=true;
        }
        
      },
      pickerClosed() {
        if(this.homeDate){
  
          this.homeDate = moment(this.homeDate).format('YYYY/MM/DD');
        }
      },
      customFormatter(date) {
        return moment(date).format('YYYY/MM/DD');
      },
      // 
      createHome(){
        firebase.database().ref('home').set(this.homeEditor)
      },
      removeUploadedImg(){
        this.uploadedImage="";
        this.inputImg="";
        document.getElementById("home-inputfile").value = '';

      },
      // 
      updateEyeCatchData(){
        const files = this.inputImg;
        const file = files[0]


        // 
        if(!file){
          this.formModalMessage=""
          this.formModal=true;
        // 
        }else if(file.type != 'image/jpeg' && file.type != 'image/gif' && file.type != 'image/png' && file.type != 'application/pdf'){
          this.formModalMessage="\njpg.gif.png.pdf"
          this.formModal=true;
        // 
        }else if(file.size > 3000000){
          this.formModalMessage="\n3MB"
          this.formModal=true;
        // 
        }else{
          this.eyeCatchBtnPush=true
          this.eyeCatchBtn=""
          firebase.storage().ref(`eyeCatch/${file.name}`).put(file)
          .then((snapshot)=>{
      
            firebase.storage().ref(snapshot.metadata.fullPath).getDownloadURL()
            .then((url)=>{
              this.filePath = url
           
              firebase.database().ref('eyeCatch').push({
                img: true,
                src:this.filePath,
                mask: "black-light",
                alt: "",
              }).then(()=>{
                this.inputImg="";
                this.uploadedImage="";
                this.eyeCatchBtn="";
                this.eyeCatchBtnPush=false;
                document.getElementById("home-inputfile").value = '';
                this.formModalMessage=""
                this.formModal=true;
                
              })
            })
          })

        }
        
        
  
      },
      // 
      selectTab(tab){
        this.activeTab=tab;
      },
      // 4
      moreView(){
        this.$emit('moreView');
      },
      // 
      removeNewsdata(){
        firebase.database().ref(`news/${this.removeModal}`).remove()
        this.newsModal=false
      },
      // 
      removeCarouseldata(){
     
        firebase.database().ref(`eyeCatch/${this.removeCarouselModal}`).remove()
        this.carouselModal=false
      },
      // 
      onFileChange(e) {

        let files = e.target.files || e.dataTransfer.files;

        if(files[0]){
                this.inputImg = files
                this.createImage(files[0]);
        }
      },
      // 
      createImage(file) {
         
          let reader = new FileReader();
          reader.onload = (e) => {
          this.uploadedImage = e.target.result;
          };
          reader.readAsDataURL(file);
      },
      // 
      handleImageAdded: function(file, Editor, cursorLocation, resetUploader) {

    

        var formData = new FormData();
        formData.append("image", file);

        firebase.storage().ref(file.name).put(file)
        .then((snapshot)=>{ // eslint-disable-line
   
         
            firebase.storage().ref(file.name).getDownloadURL()
            .then((url)=>{
                Editor.insertEmbed(cursorLocation, 'image', url)
                resetUploader()
            })
        })
        .catch((err) => {// eslint-disable-line
          this.$router.push({path:'/error'})
          
        })
    }
    },

  }
</script>

<style>
.remove-carousel{
  bottom:2rem;
  right:2rem
}

.active-font .nav-link {
  color:white;
} 
</style>