<template>
    <div>
        <h2>Achievement Settings<span></span></h2>

          <mdb-container class="mt-5">
            <mdb-row>
                <mdb-col col="sm">
                    <h3 class="mb-2"></h3>

                    <!--  -->
                    <div class="form-group">
                        <label for="name-form"><span class="ml-1 red-text"></span></label>
                        <input type="text" id="name-form" class="form-control" v-model="newDate">
                    </div>

                    <div class="d-flex flex-row-reverse">
                        <mdb-btn class="z-depth-0 mr-4" :disabled="achievementBtnPush" @click="createNewdate" color="success">{{ achievementBtn }}</mdb-btn>
                    </div>
                </mdb-col>
                <mdb-col col="sm">
                    <h3 class="mb-2"></h3>

                    <mdb-list-group>

                        <mdb-list-group-item v-for="(date, index) in dates" :key="index">
                            <router-link :to="{name:'editAchievementDetail',params:{id:index}}">{{index}}</router-link>
                            <mdb-btn class="z-depth-0" @click="removeModal=index, modal=true" outline="danger"></mdb-btn></mdb-list-group-item>
                    </mdb-list-group>
                </mdb-col>

            </mdb-row>
        </mdb-container>

        
        <!-- <ul>
            <li v-for="(date, index) in dates" :key="index">
                <router-link :to="{name:'editAchievementDetail',params:{id:index}}">{{index}}</router-link>
                <button @click="removeNewdate(index)"></button></li>
                
        </ul><br> -->

        

        <div>
            <mdb-modal :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title></mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body></mdb-modal-body>
            <mdb-modal-footer>
                <mdb-btn class="z-depth-0" color="secondary" @click.native="modal = false"></mdb-btn>
                <mdb-btn class="z-depth-0" color="danger" @click="removeNewdate()"></mdb-btn>
            </mdb-modal-footer>
            </mdb-modal>
        </div>

        <!--  -->
        <div>
            <mdb-modal :show="formModal" @close="formModal = false">
            <mdb-modal-header>
                <mdb-modal-title></mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body style="white-space:pre-wrap; word-wrap:break-word;">{{formModalMessage}}</mdb-modal-body>
            <mdb-modal-footer>
                <mdb-btn class="z-depth-0" color="success" @click.native="formModal = false">OK</mdb-btn>
            </mdb-modal-footer>
            </mdb-modal>
        </div>



    </div>
</template>

<script>
import firebase from 'firebase';
import { mdbBtn,mdbModal, mdbModalHeader, mdbModalTitle, mdbModalBody, mdbModalFooter,mdbListGroup, mdbListGroupItem, mdbContainer,
      mdbRow,
      mdbCol } from 'mdbvue';

  export default{
    name: 'editachievement',
    created(){

    },
    props:{
        dates:Object,
    },
    components: {
      mdbBtn, mdbModal, mdbModalHeader, mdbModalTitle, mdbModalBody, mdbModalFooter,mdbListGroup, mdbListGroupItem, mdbContainer,
      mdbRow,
      mdbCol
    },
    data(){
        return{
            content:'',
            newDate:'',
            modal:false,
            removeModal:'',
            formModal:false,
            formModalMessage:"",
            achievementBtn:"",
            achievementBtnPush:false,


        }
    },
    methods:{
        createNewdate(){
            if(this.newDate.length>0){
                const newDate = this.newDate
                this.achievementBtn=""
                this.achievementBtnPush=true;
                firebase.database().ref(`achievements/${newDate}`).set('').then(()=>{
                    this.achievementBtn=""
                    this.achievementBtnPush=false;
                    this.formModalMessage=""
                    this.formModal=true;
                    this.newDate="";
                })
            }else{
                this.formModalMessage="\n"
                this.formModal=true;
            }
            
        },
        removeNewdate(){
            firebase.database().ref(`achievements/${this.removeModal}`).remove();
            this.modal=false
        },
    }
  }
</script>