<template>
    <div>
        <mdb-container>
        <h2 class="mb-5">Study Settings<span></span></h2>
        <div>
            <vue-editor id="editor2" useCustomImageHandler @image-added="handleImageAdded" v-model="Content2"> </vue-editor><br>
            <div class="d-flex flex-row-reverse">
                <mdb-btn class="z-depth-0 mr-4" @click="createMainStudy()" :disabled="mainStudyBtnPush" color="success">{{ mainStudyBtn }}</mdb-btn>
            </div>

        </div>
        </mdb-container>
        

        <mdb-container>
            <mdb-row>
                <mdb-col col="sm">
                    <div class="mt-5">
                        <h3></h3>
                    </div>


                    <div class="mb-2 p-2 border border-light">

                        
                        <!--  -->
                        <div class="form-group">
                            <label for="study-title-form"><span class="ml-1 red-text"></span></label>
                            <input type="text" id="study-title-form" class="form-control" v-model="title">
                        </div>

                        <!--  -->
                        <div class="input-group mb-2">
                            <div class="custom-file">
                            <input type="file" accept="image/*" @change="onFileChange" class="custom-file-input" id="study-inputfile" aria-describedby="inputGroupFileAddon01">
                            <label class="custom-file-label" for="inputGroupFile01"></label>
                            </div>
                        </div>


                        <div class="position-relative mb-2" v-if="uploadedImage">
                            <img :src="uploadedImage.result" style="width:100%;"  />
                            <mdb-btn  class="z-depth-2 position-absolute remove-carousel" @click="removeUploadedImg()" color="danger"></mdb-btn>
                        </div>

                        <vue-editor id="editor" useCustomImageHandler @image-added="handleImageAdded" v-model="Content"> </vue-editor><br>
                        <div class="d-flex flex-row-reverse">
                            <mdb-btn class="z-depth-0 mr-4" @click="createStudy()" :disabled="studyBtnPush" color="success">{{ studyBtn }}</mdb-btn>
                        </div>
                        <!-- <mdb-btn class="z-depth-0" @click="createStudy()" color="success"></mdb-btn> -->
                        
                    </div>
                </mdb-col>
                <mdb-col col="sm">
                    <div class="mt-5">
                        <h3></h3>
                    </div>
                    
                    <!-- <ul>
                        <li v-for="(studyContent,index) in studyContents" :key='index'>
                            <router-link :to="{name:'editStudyDetail',params:{id:index}}">{{studyContent.title}}</router-link>
                            <mdb-btn @click="removeModal=index, modal=true" color="danger"></mdb-btn></li>
                            
                    </ul> -->

                    <mdb-list-group>

                        <mdb-list-group-item v-for="(studyContent,index) in studyContents" :key='index'>
                            <router-link :to="{name:'editStudyDetail',params:{id:index}}">{{studyContent.title}}</router-link>
                            <mdb-btn class="z-depth-0" @click="removeModal=index, modal=true" outline="danger"></mdb-btn></mdb-list-group-item>
                    </mdb-list-group>
                </mdb-col>

            </mdb-row>
        </mdb-container>


        

        <!--  -->
        <div>
            <mdb-modal :show="modal" @close="modal = false">
            <mdb-modal-header>
                <mdb-modal-title></mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body></mdb-modal-body>
            <mdb-modal-footer>
                <mdb-btn class="z-depth-0" color="secondary" @click.native="modal = false"></mdb-btn>
                <mdb-btn class="z-depth-0" color="danger" @click="removeStudy()"></mdb-btn>
            </mdb-modal-footer>
            </mdb-modal>
        </div>

        <!--  -->
        <div>
            <mdb-modal :show="formModal" @close="formModal = false">
            <mdb-modal-header>
                <mdb-modal-title></mdb-modal-title>
            </mdb-modal-header>
            <mdb-modal-body style="white-space:pre-wrap; word-wrap:break-word;">{{formModalMessage}}</mdb-modal-body>
            <mdb-modal-footer>
                <mdb-btn class="z-depth-0" color="success" @click.native="formModal = false">OK</mdb-btn>
            </mdb-modal-footer>
            </mdb-modal>
        </div>



        
    </div>
</template>

<script>
    import { VueEditor } from "vue2-editor";
    import firebase from 'firebase';
    import { mdbBtn, 
            mdbModal, 
            mdbModalHeader, 
            mdbModalTitle, 
            mdbModalBody, 
            mdbModalFooter,
            mdbListGroup, 
            mdbListGroupItem, 
            mdbContainer,
            mdbRow,
            mdbCol
    } from 'mdbvue';
    export default{
        data(){
            return{
                file:"",
                title:"",
                Content:"",
                Content2:"",
                filePath:"",
                uploadedImage:"",
                inputImg:"",
                removeModal:"",
                modal:false,
                formModal:false,
                formModalMessage:"",
                studyBtn:"",
                studyBtnPush:false,
                mainStudyBtn:"",
                mainStudyBtnPush:false,

            }
        },
        // 
        created(){
            this.Content2 = this.mainStudyContents
        },
        // 
        watch:{
            mainStudyContents:function () {
                console.log('someObj changed')
                this.Content2 = this.mainStudyContents
            }
        },
        components: {
            VueEditor,

            mdbBtn, 
            mdbModal, 
            mdbModalHeader, 
            mdbModalTitle, 
            mdbModalBody, 
            mdbModalFooter,
            mdbListGroup, 
            mdbListGroupItem, 
            mdbContainer,
            mdbRow,
            mdbCol
        },
        props:{
            studyContents:Object,
            mainStudyContents:String,
        },
        name: 'editStudy',
        methods:{
            createMainStudy(){
                this.mainStudyBtn=""
                this.mainStudyBtnPush=true;

                firebase.database().ref('mainStudy').set(this.Content2)
                .then(()=>{
                    this.mainStudyBtn=""
                    this.mainStudyBtnPush=false;
                    this.formModalMessage=""
                    this.formModal=true;
                })

            },
            createStudy(){
                const files = this.inputImg;
              

                if(this.title.length>0){

                    if(files.length === 0){
                    this.studyBtn=""
                    this.studyBtnPush=true;
                    firebase.database().ref('study').push({
                        filePath:null,
                        title:this.title,
                        content:this.Content}).then(()=>{
                            this.studyBtn=""
                            this.studyBtnPush=false;
                            this.file="";
                            this.title="";
                            this.Content="";
                            this.filePath="";
                            this.uploadedImage="";
                            this.inputImg="";
                            this.formModalMessage=""
                            this.formModal=true;
                            document.getElementById("study-inputfile").value = '';
                           

                        })

                    }else if(files[0].type != 'image/jpeg' && files[0].type != 'image/gif' && files[0].type != 'image/png' && files[0].type != 'application/pdf'){
                        this.formModalMessage=""
                        this.formModal=true;
                    }else if(files[0].size > 3000000){
                        this.formModalMessage="\n3MB"
                        this.formModal=true;

                    }else{
                        this.studyBtn=""
                        this.studyBtnPush=true;
                        const file = files[0]
                        firebase.storage().ref(`study/${file.name}`).put(file)
                        .then((snapshot)=>{
                        
                            firebase.storage().ref(snapshot.metadata.fullPath).getDownloadURL()
                            .then((url)=>{
                            this.filePath = url
                     
                            firebase.database().ref('study').push({
                                filePath:this.filePath,
                                title:this.title,
                                content:this.Content}).then(()=>{
                                    this.studyBtn=""
                                    this.studyBtnPush=false;
                                    this.file="";
                                    this.title="";
                                    this.Content="";
                                    this.filePath="";
                                    this.uploadedImage="";
                                    this.inputImg="";
                                    this.formModalMessage=""
                                    this.formModal=true;
                                    document.getElementById("study-inputfile").value = '';
                                })
                            })
                        })

                    }

                }else{
                    this.formModalMessage="\n"
                    this.formModal=true;

                }
                
                

                
            },
            // 
            removeUploadedImg(){
                this.uploadedImage="";
                this.inputImg="";
                document.getElementById("study-inputfile").value = '';
            },
            removeStudy(){
                
                firebase.database().ref(`study/${this.removeModal}`).remove();
                this.modal=false
                
            },
            onFileChange(e) {
          
                let files = e.target.files || e.dataTransfer.files;
        
                if(files[0]){
                    this.inputImg = files
                    this.createImage(files[0]);
                }
                
                
            },
            // 
            createImage(file) {
            
                let reader = new FileReader();
                reader.onload = (e) => {
                this.uploadedImage = e.target;
                };
            reader.readAsDataURL(file);
            },
            handleImageAdded(file, Editor, cursorLocation, resetUploader) {
             

                var formData = new FormData();
                formData.append("image", file);

                firebase.storage().ref(file.name).put(file)
                .then((snapshot)=>{ // eslint-disable-line
               
                    firebase.storage().ref(file.name).getDownloadURL()
                    .then((url)=>{
                        Editor.insertEmbed(cursorLocation, 'image', url)
                        resetUploader()
                    })
                })
                .catch((err) => {// eslint-disable-line
                     this.$router.push({path:'/error'})
                })
            }
        },
    }
</script>

<style>
    .createBtn{
        text-align:right;
    }
</style>